/* eslint-disable @typescript-eslint/no-unused-vars */
import React, { useState, useEffect } from 'react';
import { useHistory } from 'react-router-dom';

import {
  STEPS,
  Application
} from "../types";
import Forms from '../Forms';


interface Stepper {
  next?: () => void;
  prev?: () => void;
  disabled: boolean;
  Form: React.ReactElement;
  step: STEPS
};

function useStep(
  application: Application, 
  setApplication: (application: Partial<Application>) => void
): Stepper {
  let history = useHistory();
  const [step, setStep] = useState(STEPS.BORROWER);

  const finishQuote = async (causas = "", track = 0) => {
  
    //***********************************************************//
    //***********************************************************//
    // Buscar Token de Clientify
    //***********************************************************//
    //***********************************************************//

    // let Token = null
    // let Clientify = window.localStorage.getItem('Clientify') || ''
    // if (Clientify) {
    //   Token = JSON.parse(Clientify).token
    // }

    // if (!Token) {
    //   const response: Response = await fetch("/api/clientify-token", {
    //     method: 'POST'
    //   })
  
    //   // Guardamos Token de Clientify
    //   const data = await response.json();
    //   window.localStorage.setItem("Clientify",  JSON.stringify(data))
    // }

    // let IDClientify = window.localStorage.getItem('IDClientify') || ''
    
    // // Guardamos la causa del Rechazo
    // var raw = {
    //   "token": Token,
    //   "ID": IDClientify,
    //   "Tracking": "RECHAZADO POR: " + causas.toUpperCase(),
    // };

    // let method = "POST"
    // if(IDClientify) method = "PUT"

    // const response = await fetch("/api/clientify-rechazo", {
    //   method: method,
    //   body:  new URLSearchParams(raw)
    // })

    // const result = await response.json();
    // // Guardamos id del contacto en Clientify
    // window.localStorage.setItem("IDClientify",  JSON.stringify(result.id))

    history.push(`/cotizar/finalizar/?causas=${causas}&${track}`);
  }

  const API_HOST = ''; //process.env.REACT_APP_API_HOST;
  const aproach = () => history.push('/seguimiento');

  const props = {
    application, 
    setApplication
  };

  //***************************************//
  //***************************************//
  //** Guarda Informacion del Prospecto  **//
  //***************************************//
  //***************************************//
  const saveProspect = async () => {
    const { application: {
      borrower, personalReferenceRelated: refpf, personalReferenceUnrelated: refpnf, health1, health2, health3, documents, sign 
    }} = props;

    let body = {}
    const dataApp = { borrower, health1, health2, health3, documents };

    if (dataApp) {
      const { 
        id, maritalStatus, residentialNumber, province, district, prestAuto, 
        prestHip, prestTC, cashOnHand,
        department = '', specialty = '', noIp = '', residentialNumber2 = '', 
        companyPhoneNumber2 = '', companyPhoneExtension2 = '', startDate,
        company: work_name, position: work_cargo, companyAddress: work_address, 
        companyPhoneNumber: work_phone, companyPhoneExtension: work_phone_ext = '',
        previousCompany: work_prev_name, wage: work_prev_salary,
        county, street, accept: aceptaAPC
      } = borrower

      const { 
        idUrl, socialSecurityProofUrl, publicGoodProofUrl, workLetterUrl, 
        payStubUrl, apcReferencesUrl = '', apcLetterUrl = '' 
      } = documents

      const { doctor, doctorSpecialty, healthCenter } = health1

      body = {
        id_personal: id,
        phoneNumber: residentialNumber,
        civil_status: maritalStatus,
        idUrl, socialSecurityProofUrl, publicGoodProofUrl, workLetterUrl, payStubUrl, apcReferencesUrl, apcLetterUrl,
        province,
        district,
        county, 
        barrio_casa_calle: street,

        qaHealthy: JSON.stringify({ health2, health3 }),
        sign: sign.signImage || '',
        loanAuto: prestAuto,
        loanTC: prestTC,
        loanHip: prestHip,
        cashOnHand,

        work_name, 
        work_cargo, 
        work_address, 
        work_phone, 
        work_phone_ext,

        work_prev_name, 
        work_prev_salary,

        aceptaAPC,

        department, 
        specialty, 
        noIp, 
        phoneNumber_2: residentialNumber2, 
        work_phone_2: companyPhoneNumber2, 
        work_phone_ext_2: companyPhoneExtension2, 
        startDate,
        doctor, 
        doctorSpecialty, 
        healthCenter
      }
    }

    let email2 = '', telefono = '', monto = '', name = '', banco = ''

    const dataQtt = window.localStorage.getItem('quotation');

    if (dataQtt) {
      const dataAttObj = JSON.parse(dataQtt)
      const { birthDate, contractType=0, gender, jobSector, occupation = 0, profession = 0, institution = 0, retirement = 0 } = dataAttObj.borrower
      const { previousJobMonths: work_prev_month = 0, currentJobMonths: work_month } = dataAttObj.borrower
      const { wage, alloance = 0, perDiem = 0 } = dataAttObj.borrower
      const { residenceType, monthlyResidenceFee: residenceMonthly = 0, creditHistory, paymentFrecuency } = dataAttObj.borrower
      const { weight, weightUnit, height, heightUnit } = dataAttObj.borrower
      const { bank, amount = 0, term = 0 } = dataAttObj.loan
      const { email, fname, fname_2 = '', lname, lname_2 = '', origin = '', idUser = '', 
      cellPhone = '', cellPhone_2 = '', socialSecurity = '', placeOfBirth } = dataAttObj.user
      const { terms_cond, nationality = 0 } = dataAttObj.user

      monto = amount
      telefono = cellPhone
      email2 = email
      banco = bank

      name = fname + ' '
      if(fname_2) name += fname_2 + ' '
      name += lname + ' ' + lname_2

      body = {...body, estado: 1, email, name, lname, lname_2, fname, fname_2, origin_idUser: origin, 
        entity_f: bank, 
        gender, birthDate, contractType, 
        jobSector: (jobSector === 'P') ? 1 : ((jobSector === 'Pb') ? 2 : 3), 
        occupation, paymentFrecuency, profession, 
        residenceType, residenceMonthly, idUser, cellPhone, 
        cellPhone_2, socialSecurity, placeOfBirth,
        loanPP: amount, plazo: term, termConds: terms_cond ? 1: 0, nationality,
        salary: wage, honorarios: alloance, viaticos: perDiem,
        weight, weightUnit, height, heightUnit,
        work_prev_month, work_month
      }
    }

    let sponsor = "0"
    sponsor = window.localStorage.getItem('Sponsor') || "0";
    body = { ...body, sponsor }

    try {
      const result = await fetch(`${API_HOST}/adm/prospects`, {
        method: 'POST',
        body: new URLSearchParams(body)
      })
      const data = await result.json()
      const newId = data.newId

      // Info de Referencias personales Familiares
      body = { tipo: 1,
        id_prospect: newId,
        name: refpf.name,
        apellido: refpf.lastName,
        parentesco: refpf.relationship || "",
        cellphone: refpf.phoneNumber,
        phonenumber: refpf.residenceNumber,
        work_name: refpf.company,
        work_phonenumber: refpf.companyPhoneNumber,
        work_phone_ext: refpf.companyPhoneExtension || ""
      }

      fetch(`${API_HOST}/adm/ref_personales`, {
        method: 'POST',
        body: new URLSearchParams(body)
      })

      // Info de Referencias personales NO Familiares
      body = { tipo: 0,
        id_prospect: newId,
        name: refpnf.name,
        apellido: refpnf.lastName,
        parentesco: refpnf.relationship,
        cellphone: refpnf.phoneNumber,
        phonenumber: refpnf.residenceNumber,
        work_name: refpnf.company,
        work_phonenumber: refpnf.companyPhoneNumber,
        work_phone_ext: refpnf.companyPhoneExtension || ""
      }

      fetch(`${API_HOST}/adm/ref_personales`, {
        method: 'POST',
        body: new URLSearchParams(body)
      })

      // Informacion para enviar correo electrónico
      // body = {
      //   email: email2,
      //   asunto: "Solicitud de Préstamo de: >> " + name,
      //   mensaje: "Solicitud de Préstamo desde www.Finanservs.com",
      //   telefono: telefono,
      //   monto: monto,
      //   nombre: name,
      //   banco: banco,
      // }

      // fetch(`${API_HOST}/api/email`, {
      //   method: 'POST',
      //   body: new URLSearchParams(body)
      // })
    } catch (err) {
      console.log('Error: (Prospect)',err);
    }

    return
  }

  useEffect(() => {
    const dataApp = window.localStorage.getItem('application');
    if (dataApp) {
      setApplication(JSON.parse(dataApp))
      setApplication({
        borrower: {contometro: undefined}
      })
    }
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  const cellRegex = /^\d{4}(-|\s)\d{4}$/
  const phoneRegex = /^\d{3}(-|\s)\d{4}$/

  return ({
    [STEPS.BORROWER]: () => ({
      step,
      next: () => {
        // setApplication({ tracking: "Cedula y Estado Civil" })
        // saveTracking("Cedula y Estado Civil")
        window.localStorage.setItem('application', JSON.stringify(application));
        window.localStorage.setItem('finanservsIdUser', application.borrower.id || '')

        console.log(application)
        setStep(STEPS.DOCUMENTS)
      },
      disabled: (() => {
        //, phoneNumber
        const { id, maritalStatus } = application.borrower;
        return  (!id || !maritalStatus);        
      })(),      
      Form: <Forms.Borrower {...props} />
    }),
    [STEPS.DOCUMENTS]: () => ({
      step,
      next: () => {
        // setApplication({ tracking: "Imagenes de Documentos" })
        // saveTracking("Imagenes de Documentos")
        window.localStorage.setItem('application', JSON.stringify(application));
        setStep(STEPS.ADDRESS)
      },
      prev: () => setStep(STEPS.BORROWER),
      disabled: (() => {
        const { idUrl, payStubUrl, publicGoodProofUrl, socialSecurityProofUrl, workLetterUrl } = application.documents;
        return (false); // (!idUrl || !payStubUrl || !publicGoodProofUrl || !socialSecurityProofUrl || !workLetterUrl);        
      })(), 
      Form: <Forms.Documents {...props} />
    }),    
    [STEPS.ADDRESS]: () => ({
      step,
      next: () => {
        // setApplication({ tracking: "Dirección" })
        // saveTracking("Dirección")
        window.localStorage.setItem('application', JSON.stringify(application));
        setStep(STEPS.WORK)
      },
      prev: () => setStep(STEPS.DOCUMENTS),
      disabled: (() => {
        const { county, district, province, residentialNumber="", street="" } = application.borrower;

        let Err = true, Err1 = true
        if(street.length > 80) Err = false
        if(residentialNumber.length > 8) Err = false
        Err1 = phoneRegex.test(residentialNumber)

        return  (!county || !district || !province || !residentialNumber || !street || !Err || !Err1 );        
      })(),
      Form: <Forms.Address {...props} />
    }),
    [STEPS.WORK]: () => ({
      step,
      next: () => {
        // setApplication({ tracking: "Info del Trabajo" })
        // saveTracking("Info del Trabajo")
        window.localStorage.setItem('application', JSON.stringify(application));
        setStep(STEPS.PERSONAL_REFERENCE_RELATED)
      },
      prev: () => setStep(STEPS.ADDRESS),
      disabled: (() => {
        const { company="", position="", companyAddress="", companyPhoneNumber="" } = application.borrower;

        let Err = true, Err1 = true
        if(company.length > 80) Err = false
        if(position.length > 60) Err = false
        if(companyAddress.length > 60) Err = false
        if(companyPhoneNumber.length > 8) Err = false
        Err1 = phoneRegex.test(companyPhoneNumber)

        return  (!company || !position || !companyAddress ||!companyPhoneNumber || !Err || !Err1);     

        // const { company, position, companyAddress, companyPhoneNumber, previousCompany, wage } = application.borrower;
        // return  (!company || !position || !companyAddress ||!companyPhoneNumber || !previousCompany || !wage);        
      })(), 
      Form: <Forms.Work {...props} />
    }),
    [STEPS.PERSONAL_REFERENCE_RELATED]: () => ({
      step,
      next: () => {
        // setApplication({ tracking: "Ref. Personales Familia" })
        // saveTracking("Ref. Personales Familia")
        window.localStorage.setItem('application', JSON.stringify(application));
        setStep(STEPS.PERSONAL_REFERENCE_UNRELATED)
      },
      prev: () => setStep(STEPS.WORK),
      disabled: (() => {

        const { name="", lastName="", relationship="", residenceNumber="", phoneNumber="", company="", companyPhoneNumber="", companyPhoneExtension="" } = application.personalReferenceRelated;

        let Err = true, Err1 = true
        if(name.length > 20) Err = false
        if(lastName.length > 20) Err = false
        if(relationship.length > 10) Err = false
        if(residenceNumber.length > 8) Err = false
        if(phoneNumber.length > 9) Err = false
        if(companyPhoneNumber.length > 8) Err = false
        if(companyPhoneExtension.length > 4) Err = false

        Err1 = cellRegex.test(phoneNumber)
        if(Err1) Err1 = phoneRegex.test(residenceNumber)
        if(Err1) Err1 = phoneRegex.test(companyPhoneNumber)
        if(Err1) Err1 = companyPhoneExtension.length ? /^\d+$/.test(companyPhoneExtension) : true

        return  (!name || !lastName || !relationship || !residenceNumber || !phoneNumber || !company || !companyPhoneNumber || !Err || !Err1);        
      })(), 
      Form: <Forms.PersonalReferenceRelated {...props} />
    }),
    [STEPS.PERSONAL_REFERENCE_UNRELATED]: () => ({
      step,
      next: () => {
        // setApplication({ tracking: "Ref. Personales no Familia" })
        // saveTracking("Ref. Personales no Familia")
        window.localStorage.setItem('application', JSON.stringify(application));
        setStep(STEPS.HEALTH_1)
      },
      prev: () => setStep(STEPS.PERSONAL_REFERENCE_RELATED),
      disabled: (() => {
        const { name="", lastName="", residenceNumber="", phoneNumber="", company="", companyPhoneNumber="", companyPhoneExtension="" } = application.personalReferenceUnrelated;

        let Err = true, Err1 = true
        if(name.length > 20) Err = false
        if(lastName.length > 20) Err = false
        if(residenceNumber.length > 8) Err = false
        if(phoneNumber.length > 9) Err = false
        if(companyPhoneNumber.length > 8) Err = false
        if(companyPhoneExtension.length > 4) Err = false
        Err1 = cellRegex.test(phoneNumber)
        if(Err1) Err1 = phoneRegex.test(residenceNumber)
        if(Err1) Err1 = phoneRegex.test(companyPhoneNumber)
        if(Err1) Err1 = companyPhoneExtension.length ? /^\d+$/.test(companyPhoneExtension) : true

        return  (!name || !lastName || !residenceNumber || !phoneNumber || !company || !companyPhoneNumber || !Err || !Err1);        
      })(),
      Form: <Forms.PersonalReferenceUnrelated {...props} />
    }),
    [STEPS.HEALTH_1]: () => ({
      step,
      next: () => {
        window.localStorage.setItem('application', JSON.stringify(application));
        setStep(STEPS.HEALTH_2)
      },
      prev: () => setStep(STEPS.PERSONAL_REFERENCE_UNRELATED),
      disabled: (() => {
        const { doctor, doctorSpecialty, healthCenter } = application.health1;
        return  (!doctor || !doctorSpecialty || !healthCenter);        
      })(), 
      Form: <Forms.Health1 {...props} />
    }),
    [STEPS.HEALTH_2]: () => ({
      step,
      next: () => {
        window.localStorage.setItem('application', JSON.stringify(application));
        setStep(STEPS.HEALTH_3)
      },
      prev: () => setStep(STEPS.HEALTH_1),
      disabled: (() => {
        const { question1, question2, question3, question4, question5, question6, question7, question8 } = application.health2;
        return  (question1===undefined || question2===undefined || question3===undefined || question4===undefined || question5===undefined || question6===undefined || question7===undefined || question8===undefined);        
      })(),
      Form: <Forms.Health2 {...props} />
    }),
    [STEPS.HEALTH_3]: () => ({
      step,
      next: () => {
        window.localStorage.setItem('application', JSON.stringify(application));
        setStep(STEPS.SIGNED)
      },
      prev: () => setStep(STEPS.HEALTH_2),
      disabled: (() => {
        const gender = window.localStorage.getItem('gender');
        const { question1, question2, question3, question4, question5, question6, question7, question8, question9 } = application.health3;
        return  ((question1===undefined && gender==="female") || question2===undefined || question3===undefined || question4===undefined || question5===undefined || question6===undefined || question7===undefined || question8===undefined || question9===undefined);        
      })(),
      Form: <Forms.Health3 {...props} />
    }),
    [STEPS.SIGNED]: () => ({
      step,
      next: () => {
        // application.sign.signImage = undefined
        // setApplication({ tracking: "Autoriza/Firma Referencias de APC" })
        // saveTracking("Autoriza/Firma Referencias de APC")
        window.localStorage.setItem('application', JSON.stringify(application));

        const id = application.borrower.id || ""
        const data = window.localStorage.getItem('APC-'+id)

        if(data){
          const expires = new Date(JSON.parse(data).expiresIn)
          if(expires < new Date()) {
            window.localStorage.removeItem('APC-'+id)
            setStep(STEPS.APPROACH)
          } else {
            setStep(STEPS.APPROACH_2)
          }
        } else {
          setStep(STEPS.APPROACH)
        }

      },
      prev: () => setStep(STEPS.HEALTH_3),
      disabled: (() => {
        const { accept } = application.borrower;
        const { signImage } = application.sign;

        let siguiente = true;
        if (accept !== undefined && accept === 1 && signImage !== undefined && signImage !== '' && signImage !== null && signImage !== "data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAYAAAAfFcSJAAAAC0lEQVQYV2NgAAIAAAUAAarVyFEAAAAASUVORK5CYII=") {
          siguiente = false
        }

        return siguiente;
      })(),
      Form: <Forms.Signed {...props} />
    }),
    [STEPS.APPROACH]: () => ({
      step,
      next: () => {
        // setApplication({ tracking: "Finanlizado!" })
        // saveTracking("Finanlizado!")
        window.localStorage.setItem('application', JSON.stringify(application));

        const { contometro=0, endeudamiento=0, saldoPorDD=0, prestAuto=0, prestTC=0, prestHip=0 } = application.borrower;
        
        if ((contometro + endeudamiento + saldoPorDD) < 3) {
          return finishQuote(`Historial Crediticio APC (aplica?): Contometro ${contometro === 1? 'Si': 'No'}, Endeudamiento ${endeudamiento === 1? 'Si': 'No'}, Saldos por Descuento Directo ${saldoPorDD === 1? 'Si': 'No'}`);
        } else {
          saveProspect()
          aproach()
        }
      },
      prev: () => setStep(STEPS.SIGNED),
      disabled: (() => {
        const { contometro, endeudamiento, saldoPorDD } = application.borrower;
        return  (contometro===undefined || contometro===-1 || endeudamiento===undefined || saldoPorDD===undefined);  
      })(),
      Form: <Forms.Approach {...props} />
    }),
    [STEPS.APPROACH_2]: () => ({
      step,
      next: () => {
        // setApplication({ tracking: "Finanlizado!" })
        // saveTracking("Finanlizado!")
        window.localStorage.setItem('application', JSON.stringify(application));

        const { contometro=0, endeudamiento=0, saldoPorDD=0, prestAuto=0, prestTC=0, prestHip=0 } = application.borrower;
        
        if ((contometro + endeudamiento + saldoPorDD) < 3 ) {
          return finishQuote(`Historial Crediticio APC (aplica?): Contometro ${contometro === 1? 'Si': 'No'}, Endeudamiento ${endeudamiento === 1? 'Si': 'No'}, Saldos por Descuento Directo ${saldoPorDD === 1? 'Si': 'No'}`);
        } else {
          saveProspect()
          aproach()
        }
      },
      prev: () => setStep(STEPS.SIGNED),
      disabled: (() => {
        const { contometro, endeudamiento, saldoPorDD } = application.borrower;
        return  (contometro===undefined || contometro===-1 || endeudamiento===undefined || saldoPorDD===undefined);  
      })(),
      Form: <Forms.Aproach2 {...props} />
    })
  })[step]();
};

export default useStep;